.input--text {
  border: 0;
  padding: .35em;
  background-color: #fff;
  border: #084887 1px solid;
  border-radius: 0.25em; }
  .input--text:focus {
    outline-color: 0; }
  .input--text.full {
    display: block;
    width: 100%; }
  .input--text.breath {
    margin: .25em 0; }

.input--radio__group {
  display: flex;
  justify-content: center;
  align-items: center; }

.input--radio {
  visibility: hidden;
  position: absolute;
  right: 0;
  bottom: 0; }

.input--radio:checked + .label--radio::before {
  background-color: #000; }

.input--radio__set {
  padding: .5em; }

.label--radio {
  font-size: 1.125em;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0; }
  .label--radio:after {
    content: '';
    position: absolute;
    height: 0.9em;
    width: 0.9em;
    border: #000 1px solid;
    vertical-align: middle;
    border-radius: 50%;
    transition: background-color 200ms ease; }
  .label--radio::before {
    content: '';
    position: absolute;
    height: 11px;
    width: 11px;
    background-color: transparent;
    border-radius: 50%; }

.label--radio__text {
  margin-bottom: .5em;
  display: block; }

.label--checkbox {
  font-size: 1.125em;
  cursor: pointer; }

.label--checkbox::before {
  content: '';
  display: inline-block;
  height: 0.9em;
  width: 0.9em;
  border: #4A585E 1px solid;
  margin: 0 .75em;
  vertical-align: middle;
  border-radius: 3px;
  transition: background-color 200ms ease; }

.input--checkbox {
  visibility: hidden;
  position: absolute;
  right: 0;
  bottom: 0; }

.input--checkbox:checked + .label--checkbox::before {
  background-color: #56BF00; }

.alert {
  position: fixed;
  width: 80vw;
  min-width: 300px;
  padding: 1em;
  top: 2em;
  left: 10vw;
  right: 10vw;
  z-index: 50;
  border-radius: 0.25em; }
  .alert .close {
    display: none;
    cursor: pointer;
    color: #697479;
    transition: all 300ms ease-in-out; }
    .alert .close:hover {
      color: #000; }
  .alert.hide {
    display: none; }

.alert--closeable {
  position: fixed;
  width: 80vw;
  min-width: 300px;
  padding: 1em;
  top: 2em;
  left: 10vw;
  right: 10vw;
  z-index: 50;
  border-radius: 0.25em;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .alert--closeable .close {
    display: none;
    cursor: pointer;
    color: #697479;
    transition: all 300ms ease-in-out; }
    .alert--closeable .close:hover {
      color: #000; }
  .alert--closeable .close {
    display: block; }
  .alert--closeable.hide {
    display: none; }

